import React from "react";
import "../../App.css";
import Navbar from "../Navbar";
import "./Intro.css";

export default function Intro() {
  return (
    <>
      <Navbar />
      <div id="intro" class="basic-1">
        <div class="container">
          <div class="row">
            <div class="col-lg-5">
              <div class="text-container">
                <div class="section-title">INTRO</div>
                <h2>
                  We Offer Some Of The Best Business Growth Services In Town
                </h2>
                <p>
                  Launching a new company or developing the market position of
                  an existing one can be quite an overwhelming processs at
                  times.
                </p>
                <p class="testimonial-text">
                  "Our mission here at Aira is to get you through those tough
                  moments relying on our team's expertise in starting and
                  growing companies."
                </p>
                <div class="testimonial-author">Louise Donovan - CEO</div>
              </div>
            </div>
            <div class="col-lg-7">
              <div class="image-container">
                <img
                  class="img-fluid"
                  src="images/intro-office.jpg"
                  alt="alternative"
                ></img>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="cards-1 pb-0">
        <div class="container">
          <div class="row">
            <div class="col-lg-12 d-flex">
              <div class="card col-4 mb-3">
                <span class="fa-stack">
                  <span class="hexagon"></span>
                  <i
                    class="fa fa-envelope-open fa-stack-1x"
                    aria-hidden="true"
                  ></i>
                </span>
                <div class="card-body">
                  <h4 class="card-title">Environment Analysis</h4>
                  <p>
                    The starting point of any success story is knowing your
                    current position in the business environment
                  </p>
                </div>
              </div>

              <div class="card col-4 mb-3">
                <span class="fa-stack">
                  <span class="hexagon"></span>
                  <i
                    class="fa fa-address-book-o fa-stack-1x"
                    aria-hidden="true"
                  ></i>
                </span>
                <div class="card-body">
                  <h4 class="card-title">Development Planning</h4>
                  <p>
                    After completing the environmental analysis the next stage
                    is to design and plan your development strategy
                  </p>
                </div>
              </div>

              <div class="card col-4 mb-3">
                <span class="fa-stack">
                  <span class="hexagon"></span>
                  <i class="fa fa-car fa-stack-1x" aria-hidden="true"></i>
                </span>
                <div class="card-body">
                  <h4 class="card-title">Execution and Evaluation</h4>
                  <p>
                    In this phase you will focus on executing the actions plan
                    and evaluating the results after each marketing campaign
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
